/* navigation */
#menu_toggle {
  display: none;
  float: right;
  position: relative;
  overflow: hidden;
  margin-top: 10px;
  margin-left: 25px;
  margin-bottom: 10px;
  width: 24px;
  height: 24px;
  font-size: 0;
  text-indent: -9999px;
  appearance: none;
  background-color: transparent;
  cursor: pointer;
  transition: background 0.3s;
  transition: margin 0.2s;
}
@media only screen and (max-width: 819px) {
  #menu_toggle {
    display: block;
  }
}
@media only screen and (max-width: 380px) {
  #menu_toggle {
    margin-left: 5px;
  }
}
#menu_toggle span {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 2px;
  background: white;
  transition: background 0s 0.3s;
}
#menu_toggle span:before, #menu_toggle span:after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  content: "";
  transition-duration: 0.3s, 0.3s;
  transition-delay: 0.3s, 0s;
}
#menu_toggle span:before {
  top: -7px;
  transition-property: top, transform;
}
#menu_toggle span:after {
  bottom: -7px;
  transition-property: bottom, transform;
}
#menu_toggle.is-active {
  background-color: transparent;
}
#menu_toggle.is-active span {
  background: none;
}
#menu_toggle.is-active span:before {
  top: 0;
  transform: rotate(45deg);
}
#menu_toggle.is-active span:after {
  bottom: 0;
  transform: rotate(-45deg);
}
#menu_toggle.is-active span:before, #menu_toggle.is-active span:after {
  transition-delay: 0s, 0.3s;
}

